import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { RatingGaugeWebElement } from './RatingGaugeWebElement';
export { RatingGaugeWebElement };

/**
 * Retrieve an instance of [[RatingGaugeWebElement]].
 * @example
 * ```javascript
 * import { findRatingGauge } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findRatingGauge(driver, By.id('my-oj-c-rating-gauge'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findRatingGauge(driver: DriverLike, by: By): Promise<RatingGaugeWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type RatingGaugeWebElement
  if (!(webEl instanceof RatingGaugeWebElement)) {
    const tagName = await webEl.getTagName();
    throw Error(
      `findRatingGauge(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${RatingGaugeWebElement.name}`
    );
  }
  return webEl;
}

register('oj-c-rating-gauge', RatingGaugeWebElement);
